.header {
  background: #fff url("http://wp-shop/wp-content/uploads/2020/02/background.jpg") center 0px no-repeat;
  -webkit-background-size: cover;
  background-size: cover; }

.nav-bar {
  &-wrap {
    display: flex;
    justify-content: space-between; }
  &__logo {
    margin-top: 40px; }
  &__menu {
    display: flex;
    justify-content: space-between;
    align-content: center; }
  &__menu-container {
    display: flex;
    li {
      margin: 40px 25px 0px 25px;
      list-style: none;
      a {
        color: #fff;
        text-decoration: none; } } }
  &__menu-basket {
    margin-top: 40px; } }

.hero {
  padding-top: 230px;
  padding-bottom: 270px;
  &__header {
    color: $f-white;
    margin: 0 auto;
    display: block;
    font-size: 60px;
    text-align: center; }
  &__text {
    color: $f-white;
    margin: 20px auto 0px auto;
    display: block;
    font-size: 20px;
    text-align: center;
    max-width: 600px; } }
.arrow {
  text-align: center;
  padding-bottom: 50px;
  &__img {
    width: 36px;
    height: 25px; } }
