.filter {
  background-color: #f5f6f7;
  &-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 145px;
    flex-wrap: wrap; }
  &__btn {
    background-color: $f-brown;
    color: $f-white;
    padding: 20px 35px;
    border-radius: 5px; }
  &__category {
    display: flex;
    align-items: center; }
  &__category-item {
    margin-right: 40px;
    display: flex;
    align-items: center;
    >label {
      font-size: 18px;
      text-transform: uppercase;
      font-weight: bold;
      margin-left: 20px; } } }


.category-checkinput {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  overflow: hidden; }


.check-box {
  width: 25px;
  height: 25px;
  background-color: #fff;
  border-radius: 2px;
  border: 2px solid #cccccc; }

.checkbox-checked {
  background: url('http://wp-shop/wp-content/themes/wp-shop-theme/assets/img/top-sidebar/correct.svg') 2px 2px no-repeat;
  -webkit-background-size: 20px;
  background-size: 20px;
  border: 2px solid $f-brown; }
